//navbar
.navbar {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    li {
        padding: 6px 16px;
        font-size: 1.8rem;
        cursor: pointer;
        list-style: none;
        &:hover {
            opacity: 0.8;
        }
    }
    .category {
        &:hover {
            background-color: #3f3f4e;
        }

        .item-category {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
}
