.wrapper {
    position: relative;
    background-color: var(--background-header);
    margin: 8px;
    min-height: 200px;
}

.header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 8px 50px;
    font-size: 1.6rem;
}

.hightlight {
    width: 90%;
    height: 1px;
    margin: auto;
    background-color: var(--color-yellow);
    margin-bottom: 8px;
}

.flim-list {
    margin: 0 16px;
}

.item {
    margin-bottom: 24px;
}

.show-all {
    margin: auto;
    display: block;
    text-align: center;
    width: 200px;
    padding: 4px 18px 18px;
    font-size: 1.8rem;
    &:hover {
        opacity: 0.8;
    }

    .show-title {
        padding-left: 6px;
    }
}

//movie-item
.wrapper-item {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    background-color: #1a1a1a;
    margin: 8px 0;
    transition: transform 0.3s ease;
    &:hover {
        animation: growthImage ease 0.3s forwards;
    }
}

@keyframes growthImage {
    to {
        transform: translateY(-1px);
        opacity: 0.8;
    }
}
.film-cover {
    position: relative;
    width: 100%;
    height: 240px;
    max-height: 268px;
    overflow: hidden;
    margin-bottom: 5px;
}

.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 4px;
}
.episode {
    position: absolute;
    max-width: 100%;
    top: 0;
    left: 0;
    text-align: center;
    border-top-left-radius: 4px;
    background-color: rgba(201, 62, 54, 0.85);
    padding: 4px;
    font-size: 1.4rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.name-flim {
    display: block;
    width: 100%;
    font-size: 1.6rem;
    font-weight: 600;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    color: var(--color-yellow);
    padding: 4px 6px;
    line-height: 1.8rem;
}
.origin-name {
    display: block;
    width: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    padding: 4px 6px 0;
    opacity: 0.7;
    line-height: 1.8rem;
    font-style: oblique;
}

.footer {
    width: 100%;
    display: flex;
    justify-content: center;

    padding: 30px 0;
    .pagination {
        width: 50%;
        font-size: 10rem;
    }
}

.loading {
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 10;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.55);

    .icon-loading {
        display: block;
        margin-top: 20vh;
        font-size: 38px;
        animation: rotateIcon 0.3s linear infinite;
    }
}

@keyframes rotateIcon {
    to {
        transform: rotate(360deg);
    }
}

.load-false {
    width: 100%;
    padding: 12px;
    font-size: 2.4rem;
    text-align: center;
}
