.wrapper {
    display: flex;
    align-items: center;
    margin: 12px;

    &:hover {
        background-color: #3f3f4e;
        cursor: pointer;
    }
}

.iamge {
    width: 46px;
    min-width: 46px;
    height: 56px;
    min-height: 46px;
    object-fit: cover;
}

.body {
    width: 100%;

    font-size: 1.4rem;
    padding: 0 6px;
}

.title {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2; /* Số dòng hiển thị */
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.4rem;
}

.date {
    display: block;
    padding-top: 4px;
    opacity: 0.8;
    font-style: oblique;
}
