//search
.search {
    background-color: #303137;
    width: 300px;
    border-radius: var(--border-radius);
    border: 1px solid #555;
    display: flex;
    align-items: center;

    input {
        flex: 1;
        background-color: #303137;
        border-radius: var(--border-radius);
        padding: 6px 12px;
        font-size: 1.4rem;
    }

    .search-btn {
        position: relative;
        background-color: transparent;
        padding: 6px 12px;
        border-top-right-radius: var(--border-radius);
        border-bottom-right-radius: var(--border-radius);
        &:hover {
            background-color: #555;
            cursor: pointer;
        }

        &::before {
            content: '';
            display: block;
            position: absolute;
            width: 1px;
            padding: 10px 0;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            background-color: #555;
        }
    }
}

.search-title {
    padding: 8px 8px 0;
    font-style: oblique;
}

.search-item {
    width: 100%;
    padding-left: 8px;
    line-height: 3.8rem;
    cursor: pointer;

    &:hover {
        background-color: #3f3f4e;
    }
}

.search-failed {
    padding: 8px;
}

.footer {
    margin-bottom: 42px;
}

.show-all {
    width: 100%;
    position: absolute;
    bottom: 0;
    text-align: center;
    background-color: #343439;
    padding: 4px;
    user-select: none;
    cursor: pointer;
    &:hover {
        background-color: #3f3f4e;
    }
}
