.wrapper {
    position: relative;
    margin: 12px;
    width: 120px;
    min-width: 120px;
    height: 150px;
    min-height: 150px;
    background-color: var(--white);
    color: var(--black);
    border-radius: 4px;
}

.logo {
    margin: 4px;
    height: 88px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    .logo-img {
        width: 100%;
        object-fit: cover;
    }

    .logo-icon {
        font-size: 4rem;
    }
}

.name {
    text-align: center;
    position: absolute;
    bottom: 0;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    width: 100%;
    overflow: hidden;
    padding: 4px;
    font-weight: 500;
}
