.wrapper {
    width: 90%;
    height: 320px;
    border-radius: 8px;
    overflow: hidden;
    margin-top: 8px;
}

.item {
    display: flex;
    justify-content: center;
    position: relative;
}
.title {
    position: absolute;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 70%;
    height: 30px;
    background-color: rgba(201, 62, 54, 0.85);
    border-radius: 6px;
    font-size: 1.4rem;
}
.name {
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
    padding: 4px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    max-height: 50px;
    height: 50px;
    background-color: rgba(75, 28, 26, 1);
    font-size: 1.7rem;
}
