.wrapper {
    position: relative;
    bottom: 0;
    width: 100%;
    background-color: #1a1a1a;
    padding: 8px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.logo {
    height: var(--height-header);
    display: flex;
    align-items: center;
    min-width: 210px;
    width: 210px;
}

.logo-img {
    height: 100%;
}

.logo-title {
    font-size: 2.4rem;
    font-style: italic;
    text-shadow: 2px 2px 4px #000;
}

.list {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.item-main {
    width: 50%;
    p {
        padding: 0 8px 12px;
    }
}

.wrap-item {
    display: flex;

    .item {
        margin: 24px 8px;
        list-style: none;

        span {
            font-size: 1.8rem;
            font-style: italic;
            font-weight: 600;
        }
        ul {
            list-style-type: none;
            margin-top: 16px;
        }
    }
}

.qc {
    font-size: 3rem;
    color: var(--color-yellow);
    font-style: italic;
}

.copyright {
    display: block;
    width: 100%;
    text-align: center;
    opacity: 0.6;
    font-style: italic;
}

@media (max-width: 1024px) {
    .item-main {
        width: 100%;
    }
}
