.wrapper {
    width: 128px;
    margin: 8px;
    background-color: var(--background-content);
    padding: 4px;
    border-radius: 4px;
}

.profile {
    width: 120px;
    min-width: 120px;
    height: 160px;
    min-height: 160px;
    .img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .icon {
        font-size: 8rem;
        text-align: center;
    }
}

.name {
    text-align: center;
    font-size: 1.4rem;
    padding: 4px;
}

.lighthight {
    width: 40%;
    height: 0.8px;
    background-color: var(--color-yellow);
}

.character {
    padding: 4px;
    font-size: 1.4rem;
}
