.wrapper {
    width: 1200px;
    max-width: 100%;
    background-color: var(--background-content);
    padding: 18px;
}

.poster {
    display: flex;
    flex-direction: column;
    align-items: center;
    .image {
        width: 100%;
        height: 100%;
        min-height: 350px;
        object-fit: cover;
    }
    .control {
        display: block;
        width: 120px;
        padding: 8px 0;
        margin: 8px;
        text-align: center;
        border-radius: 90px;
        background-color: var(--color-yellow);
    }
}

.header {
    margin-bottom: 14px;
    .header-title {
        .group-title {
            display: flex;

            h3 {
                font-size: 3rem;
                font-weight: 400;
                line-height: 3.8rem;
            }
            .vote {
                padding: 7px 12px 0;
                color: var(--color-yellow);
                font-style: italic;
                font-size: 2rem;
            }
        }

        .sub-title {
            display: block;
            font-style: italic;
            font-size: 2rem;
            opacity: 0.8;
            margin-top: 12px;
        }
    }

    .lighthight {
        height: 0.7px;
        width: 200px;
        background-color: var(--color-yellow);
        opacity: 0.8;
        margin: 4px 0 16px;
    }
    p {
        padding: 6px 0;
        opacity: 0.8;

        a {
            text-decoration: underline;
        }
    }
}

.overview {
    position: relative;
    p {
        opacity: 0.8;
    }
    padding-left: 8px;
    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 1px;
        height: 100%;
        left: -4px;
        background-color: var(--color-yellow);
    }
}
.cast,
.companies {
    padding: 18px 6px;
}
