.header {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 54px;
    background-color: var(--background-header);
    z-index: 1999;
    &:after {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: var(--color-yellow);
        top: 100%;
    }
}

.inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: var(--container);
    max-width: 100%;
    padding: 0 var(--padding-space);
}

.logo {
    height: var(--height-header);
    display: flex;
    align-items: center;
    min-width: 210px;
    width: 210px;
}

.logo-img {
    height: 100%;
}

.logo-title {
    font-size: 2.4rem;
    font-style: italic;
    text-shadow: 2px 2px 4px #000;
}

.wrapper-nav {
    display: flex;
    align-items: center;
}

////////////////////////////////////////////////////////
//responsive
.nav-mobile {
    display: none;
}
//PC

//mobile
@media (max-width: 740px) {
    .wrapper-nav {
        position: absolute;
        top: 100%;
        width: 100%;
        padding: 18px 0 8px;
        display: flex;
        flex-direction: column-reverse;
        background-color: var(--background-content);
        display: none;
    }
    .nav-mobile {
        display: block;
        padding: 12px;
        font-size: 2rem;
        cursor: pointer;
    }
}
