.wrapper {
    background-color: var(--background-content);
    padding: 4px;
}

.wrapper-header {
    width: 100%;
    display: flex;
    justify-content: center;
}

.header {
    width: 80%;
    .title {
        padding: 12px 0;
        font-size: 2rem;
        opacity: 0.8;
        font-style: oblique;
    }
    .video {
        position: relative;
        padding-bottom: 56.25%;
        height: 0;

        .frameVideo {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
    }
}

.episode {
    margin: 24px;
    border: 1px solid var(--color-yellow);
    padding: 8px;
    background-color: var(--background-header);
    display: flex;
    flex-wrap: wrap;
}
.btn-episode {
    width: 120px;
    min-width: 120px;
    max-width: 120px;
    margin: 8px;
    padding: 6px;
    border-radius: 4px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
    cursor: pointer;
    user-select: none;
    background-color: var(--background-content);
}
